@import url(https://fonts.googleapis.com/css?family=Muli:300,300i,400,400i,600,600i,700,700i,800,800i,900,900i);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html, body, #root, .App {
  color: white;
  font-family: Muli;
  font-weight: 200;
  font-style: italic;
  font-size: 16px;
}

.bold-and-underline{
  font-weight: 800;
  border-bottom: 1px solid white;  
}
.screenbutton {
  width: 100vw;
  height: 100vh;
  /* -webkit-transition: background-color 2s;
  transition: background-color 2s; */
  /* background-color: aquamarine; */
  opacity: 0.4;
  position: absolute;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.big-message {
  width: 260px;
  font-weight: 800;
  font-style: normal;
  color: white;
  font-size: 50px;
}

.user-message-player {
  width: 260px;
  font-weight: 800;
  font-style: normal;
  color: white;
  font-size: 45px;
}

.user-message-text{
  width: 260px;
  font-weight: 800;
  font-style: normal;
  color: white;
  font-size: 30px;
}

.tap-navbar {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 10px;
  align-items: center;
  z-index: 2;
}

.tap-navbar a{
  height: 32px;
  display: flex;
  align-content: center;
}

.tap-navbrand {
  display: flex;
  align-items: center;
}

.tap-navbrand img{
  display: block;
}
.home-pages {
  flex: 1 0;
  width: 100%;
  display: flex;
  position: relative;
}

.tap-main-container {
  flex: 1 0;
  margin: 15px auto 0 auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: 0 25px 0 25px;
  box-sizing: border-box;
  justify-content: center;
  overflow-y: scroll;
  position: relative;
}

.tap-main-container figure {
  width: 100%;
  max-width: 800px;
  margin-bottom: 20px;
}
.tap-homelogo {
  display: block;
  width: 100%;
  margin-bottom: 15px;
}

.tap-homelogo img {
  max-width: 560px;
  width: 100%;
}

.tap-homelogo.one {
  opacity: 1;
}
.tap-homelogo.two {
  opacity: 0.8;
}
.tap-homelogo.three {
  opacity: 0.6;
}
.tap-homelogo.four {
  opacity: 0.4;
}
.tap-homelogo.five {
  opacity: 0.2;
}

.tap-hometext {
  font-size: 18px;
  width: 260px;
}

.tap-hometext.small {
  font-size: 20px;
  width: 200px;
  margin-bottom: -30px;
}

.tap-input {
  border-radius: 30px;
  border: none;
  width: 260px;
  box-sizing: border-box;
  padding: 20px 25px;
  font-family: "Muli";
  font-weight: 800;
  font-size: 20px;
  color: #cfc18e;
  margin-top: 16px;
  text-align: center;
}

.tap-input::-webkit-input-placeholder {
  color: #cfc18e;
}

.tap-input::-ms-input-placeholder {
  color: #cfc18e;
}

.tap-input::placeholder {
  color: #cfc18e;
}

.tap-input:focus , .tap-input-player:focus, .tap-bttn-addplayer:focus{
  outline: none;
}

.tap-input-player {
  display:block;
  background-color: transparent;
  border: none;
  width: 200px;
  box-sizing: border-box;
  padding: 6px;
  font-family: "Muli";
  font-weight: 300;
  font-size: 16px;
  color: white;
  border-bottom: 1px solid white;
}

.tap-input-player::-webkit-input-placeholder {
  color: white;
}

.tap-input-player::-ms-input-placeholder {
  color: white;
}

.tap-input-player::placeholder {
  color: white;
}

.add-player {
  display: flex;
  justify-content: space-between;
}

.tap-bttn-addplayer {
  display: block;
  border: 3px solid white;
  height: 32px;
  width: 32px;
  background-color: transparent;
  border-radius: 50px;
  color: white;
  font-weight: 800;
  font-size: 20px;
  padding-bottom: 2px;
}

.new-player-box {
  height: 32px;
  width: 260px;
  display: flex;
  justify-content: space-between;

  margin-bottom: 16px;
}

.new-player-name {
  text-align: left;
  width: 220px;
  box-sizing: border-box;
  padding: 6px;
  font-family: "Muli";
  font-weight: 700;
  font-size: 20px;
  color: white;
  line-height: 20px;
  font-style: normal;
}

.tap-input.roomname{
  background-color: white;
  text-align: center;
  color: #8ECF92;
  font-style: normal;
  line-height: 25px;
}

.button.is-loading.superspinner{
  background-color: transparent;
  border: none;
}

.button.is-loading.superspinner::after{
  border: 3px solid white;
  border-right-color: transparent;
  border-top-color: transparent;
}

.tap-onyourmarks {
  width: 260px;
  font-family: "Muli";
  font-weight: 900;
  font-size: 50px;
  font-style: normal;
  margin-top: 8px;
  line-height: 63px;
}

.tap-players-onmarks {
  width: 260px;
  font-family: "Muli";
  font-weight: 800;
  font-size: 20px;
  font-style: italic;
  line-height: 25px;
}
.outline-button {
  position: relative;
  display: block;
  text-align: center;
  margin: 16px auto 30px auto;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  width: 270px;
  height: 52px;
  border-radius: 30px;
  background: transparent;
  border: 3px solid white;
  color: white;
}

.outline-button:focus {
  outline: none;
}

.on-footer {
  /* position: absolute;
  right: 0;
  bottom: 0;
  left: 0; */
}

.bottom-message {
  font-weight: 300;
  font-style: normal;
  text-align: center;
  margin-top: 26px;
  height: 24px;
}

.outline-button.filled{
  border: none;
  background: white;
}
html, body, #root, .App {
  min-height: 100vh;
}

.tap-main {
  min-height: 100vh;
  display: flex;
  flex-flow: column nowrap;
  background-color: red;
  transition: background-color 1000ms;
}

.example-enter {
  right: -100%;
  /* transform: translate(100%); */
}
.example-enter.example-enter-active {
  /* transform: translate(0%); */
  right: 0%;
  transition: right 1000ms ease-in-out;
}
.example-exit {
  right: 0%;
  /* transform: translate(0%); */

}
.example-exit.example-exit-active {
  right: 100%;
  /* transform: translate(-100%); */
  transition: right 1000ms ease-in-out;
}

.connect-button {
  width: 100vw;
    height: 100vh;
    display: flex;
    position: relative;
}

.fademessage-enter{
  opacity: 0;
}

.fademessage-enter.fademessage-enter-active{
  opacity: 1;
  transition: opacity 100ms ease-in;
}

.fademessage-exit{
  opacity: 1;
}

.fademessage-exit.fademessage-exit-active{
  opacity: 0;
  transition: opacity 1000ms ease-out;
}
.game-server {
  color: white;
}
